import React, { useRef, useState, useEffect } from "react";
import { Col, Row, Container, Button, Form } from "react-bootstrap";
import { navigate } from "gatsby";

import "../components/pages-css/sCita.css";
import Layout from "../components/layout.js";
import Seo from "../components/seo";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Keyboards from "../components/Keyboards.js";
import Timer from "../components/timer.js";

import imagen from "../images/iconos/btn_cliente_nuevo.svg";

export default function ComentariosPaquetes(props){
    const [show, setShow] = useState(false);
    const [modal, setModal] = useState(false);
    const [errorInput, setErrorInput] = useState(false);
    const [error, setError] = useState(0);
    const [click, setClick] = useState(false);
    const [optionsKeyboard, setOptionsKeyboard] = useState({
        inpName: "",
        layoName: "default",
        name: "",
        value: {}
    })
    const refForm = useRef();

    useEffect(() => {
        if (typeof window !== 'undefined') {
            //console.log('we are running on the client', localStorage.getItem("ComentariosP"));
            setOptionsKeyboard({
              ...optionsKeyboard,
              value: {
                ComentariosP: localStorage.getItem("ComentariosPaq") || "",
              }
            })
        } else {
            //console.log('we are running on the server');
        }
    }, [])

    function continuar(){
        if(optionsKeyboard.value.ComentariosP === "" || optionsKeyboard.value.ComentariosP === undefined){
            setError(0);
            setModal(true);
            setErrorInput(true);
       } else {
            //console.log("Me han pulsado jiji: ", optionsKeyboard.value.ComentariosP);
            localStorage.setItem('ComentariosPaq', optionsKeyboard.value.ComentariosP);
            //navigate("/servicios-adicionales");
            navigate("/resumen-servicios");
       }
    }

    return(
        <Layout backUrl={"/realizar-servicio"}>
            <Seo title="Auto servicio" />
            <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
            {<Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>}
                <Row className="row-cita-arriba">
                    <div className="container-cita">
                        <Row>
                            <Col className="titulo-cita"><h1 className="titulo-h1">Comentarios de paquetes</h1></Col>
                        </Row>
                        <Container className="p-5">
                            <Row className="titulo-cita">
                                <Col lg={4} style={{display: 'flex', justifyContent: 'center'}}>
                                    <Row style={{backgroundColor: '#deecff', borderRadius: 50, margin: 10, padding: 5}}>
                                        <img src={imagen} style={{height: '150px', marginBottom: 0, padding: 10}}/> 
                                        <div style={{display: 'flex', justifyContent: 'center'}}><h5 className="titulo-h1">Comentarios</h5></div>
                                    </Row>
                                </Col>
                                <Col lg={8} style={{display: 'flex', justifyContent: 'center'}}>
                                    <Form.Control
                                        as="textarea" 
                                        placeholder="CuÃ©ntame... " 
                                        rows={6} 
                                        id={errorInput === true ? "inputinputDiagnosticoError" : "inputDiagnostico"}
                                        onClick={() => setShow(true)}
                                        onFocus={() => setOptionsKeyboard({...optionsKeyboard, inpName: "ComentariosP"})}
                                        value={optionsKeyboard.value.ComentariosP || ""}
                                    />
                                </Col>
                            </Row>
                            <Button ref={refForm} onClick={() => continuar()} style={{display: 'none'}}></Button>
                            <center className="pt-4">
                                <Keyboards options={optionsKeyboard} setOptionsKeyboard={setOptionsKeyboard} optionsKeyboard={optionsKeyboard}/>
                            </center>
                        </Container>
                    </div>
                </Row>
                <Row className="row-cita-abajo">
                    <VideoSeccion refForm={refForm} none="tiene"/>
                </Row>
            </Container>
            <ModalEmergente modal={modal} error={error} setModal={setModal} setClick={setClick}/>
        </Layout>
    )

}